:root {

  --primary-color: #6765f0;
  --headding-color: #132742;
}

body {
  font-family: 'Times New Roman', Times, serif;
}
a {
  text-decoration: none;
}
section {
  padding: 50px 0px;
  color: unset;
}

a:hover{
  color: unset;
}

link:hover{
  color: unset;
}
li {
  list-style: none;
}
.container{
  width: 1300px;
  padding: 0px 15px;
  margin: auto;
}

.section_title{
  color: var(--heading-color);
  font-weight: 700;
}

.register-btn {
  background: var(--primary-color);
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-family: var(--font-name);
  font-family: 1rem;
  font-weight: 700;
  outline: none;
  padding: 7px 15px;
}